.projects_container {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 100px 0px;
}
.projects_title {
  font-size: 80px;
  font-weight: 700;
  margin-bottom: 100px;
}
.projects_list_container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 60%;
  height: 100%;
}
.projects_item_container {
  width: 100%;
  height: 500px;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  margin-bottom: 100px;
}
.projects_item_project_container {
  width: 50%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.projects_item_img_container {
  width: 100%;
  height: 400px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: black;
  border: none;
  border-radius: 20px 0px 0px 20px;
  cursor: pointer;
}
.projects_item_img_container a{
  object-fit:cover;
  width: 100%;
  height: 100%;
  border: none;
  border-radius: 20px 0px 0px 20px;
}
.projects_item_img {
  object-fit:cover;
  width: 100%;
  height: 100%;
  border: none;
  border-radius: 20px 0px 0px 20px;
}
.projects_item_button_container {
  height: 100px;
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.projects_item_button {
  padding: 8px 16px;
  background-color: white;
  color: #A400D8;
  font-size: 20px;
  border: none;
  border-radius: 10px;
  cursor: pointer;
}



.projects_item_description_container {
  width: 50%;
  height: 400px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  padding: 30px;
  background-color: white;
  border: none;
  border-radius: 0px 20px 20px 0px;
}
.projects_item_title {
  font-size: 38px;
  font-weight: 600;
}
.projects_item_description {
  margin-top: 20px;
}




@media screen and (max-width: 1700px) {
  .projects_container {
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding: 100px 0px;
  }
  .projects_title {
    font-size: 40px;
    font-weight: 700;
    margin-bottom: 50px;
  }
  .projects_list_container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
    height: 100%;
  }
  .projects_item_container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 120px;
  }
  .projects_item_project_container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
  }
  .projects_item_img_container {
    width: 80%;
    height: 250px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: black;
    border: none;
    border-radius: 10px;
    cursor: pointer;
  }
  .projects_item_img_container a{
    object-fit:contain;
    width: 100%;
    height: 100%;
    border: none;
    border-radius: 10px;
  }
  .projects_item_img {
    object-fit:contain;
    width: 100%;
    height: 100%;
    border: none;
    border-radius: 10px;
  }
  .projects_item_button_container {
    height: 150px;
    width: 80%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
  }
  .projects_item_button {
    padding: 8px 16px;
    background-color: white;
    color: #A400D8;
    font-size: 20px;
    border: none;
    border-radius: 10px;
    cursor: pointer;
  }
  .projects_item_description_container {
    width: 80%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    padding: 20px;
    background-color: white;
    border: none;
    border-radius: 20px;
  }
  .projects_item_title {
    font-size: 25px;
    font-weight: 600;
  }
  .projects_item_description {
    margin-top: 20px;
  }
}