.navbar_container {
  width: 100%;
  height: 10%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  position: fixed;
  background-color: black;
}
.navbar_menu {
  padding: 26px;
  font-size: 24px;
  cursor: pointer;
}

@media screen and (max-width: 1700px) {
  .navbar_container {
    justify-content: center;
  }
  .navbar_menu {
    padding: 25px;
    font-size: 25px;
  }
}