.home_container {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 50px 400px;
}
.home_profil_container {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.home_description_container {
  display: flex;
  flex-direction: column;
  width: 60%;
}
.home_soliditydeveloper {
  font-size: 70px;
  font-weight: 700;
}
.home_name {
  font-size: 40px;
  font-weight: 500;
  color: white;
}
.home_icon_container {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-left: 5px;
  margin-top: 20px;
}
.home_icon {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  margin-right: 20px;
  border-radius: 10px;
}
.home_icon a {
  display: flex;
  justify-content: center;
  align-items: center;
}
.home_icon_img_linkedin {
  width: 100%;
  height: 100%;
  object-fit: contain;
  border-radius: 10px;
}
.home_icon_img_github {
  width: 100%;
  height: 100%;
  object-fit: contain;
  background-color: white;
  border-radius: 10px;
}
.home_resume {
  padding: 11px 22px;
  font-size: 23px;
  border: none;
  border-radius: 10px;
  cursor: pointer;
}
.home_part2_container {
  height: 100%;
  width: 40%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.home_image_container {
  height: auto;
  width: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  border-radius: 50%;
}
.home_image {
  object-fit: contain;
  width: 100%;
  height: 100%;
  border: none;
  border-radius: 50%;
}


@media screen and (max-width: 1700px) {
  .home_container {
    margin-bottom: 150px;
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;
    padding-top: 100px;
  }
  .home_profil_container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .home_description_container {
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: center;
    justify-content: center;
  }
  .home_soliditydeveloper {
    font-size: 35px;
    font-weight: 700;
    margin-bottom: 15px;
  }
  .home_name {
    font-size: 20px;
    font-weight: 500;
    color: white;
    margin-top: 10px;
  }
  .home_icon_container {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-left: 5px;
    margin-top: 40px;
  }

  .home_part2_container {
    margin-top: 0px;
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .home_image_container {
    padding: 40px;
    height: auto;
    width: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
    border-radius: 50%;
  }
  .home_image {
    object-fit: contain;
    width: 100%;
    height: 100%;
    border: none;
    border-radius: 50%;
  }
}