.tools_container {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 100px 0px;
}
.tools_title {
  font-size: 80px;
  font-weight: 700;
}
.tools_list_container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  padding-top: 100px;
}
.tools_list {
  width: 33%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  font-size: 30px;
}
.tools_item {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 30px;
  border-radius: 50px;
  background-color: white;
  width: 500px;
  padding: 10px 20px;
  box-shadow: 10px 10px 10px #A400D8;
}
.tools_img_container {
  width: 60px;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 10px;
}
.tools_img {
  object-fit:contain;
  width: 100%;
  height: 100%;
  background-color: white;
}



@media screen and (max-width: 1700px) {
  .tools_container {
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding: 100px 25px;
    margin-bottom: 100px;
  }
  .tools_title {
    font-size: 35px;
    font-weight: 700;
  }
  .tools_list_container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    padding-top: 30px;
  }
  .tools_list {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    font-size: 20px;
  }
  .tools_item {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 30px;
    border-radius: 50px;
    background-color: white;
    width: 100%;
    padding: 10px 20px;
    box-shadow: 10px 10px 10px #A400D8;
  }
  .tools_img_container {
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 5px;
  }
}